/**
 * 服务器地址
 */
let domain = 'https://api.it120.cc/tz/';

/**
 * 供外部post请求调用
 */
function post(url, data) {
  request(url, data, "POST");
}

/**
 * 供外部get请求调用
 */
function get(url, data) {
  request(url, data, "GET");
}

/**
 * function: 封装网络请求
 * @url URL地址
 * @params 请求参数
 * @method 请求方式：GET/POST
 * @onStart 开始请求,初始加载loading等处理
 * @onSuccess 成功回调
 * @onFailed  失败回调
 */

function request(url, data, method) {
  wx.showLoading({
    title: '加载中...'
  });
  return new Promise((resolve, reject) => {
    wx.request({
      url: domain + url,
      data: data,
      method: method,
      header: {
        'content-type': 'application/json'
      },
      success: function (res) {
        if (res.statusCode != 200) {
          wx.showToast({
            title: "网络出错，稍后再试",
            icon: "none"
          });
          return;
        }
        resolve(res);
      },
      fail: function (error) {
        reject(error);
      },
      complete: function () {
        wx.hideLoading();
      }
    });
  });

}

export default {
  post,
  get,
  request
}



